import { NavController, PopoverController } from 'ionic-angular';
import { Component, Input } from '@angular/core';

@Component({
  selector: 'post-noticia',
  templateUrl: 'post-noticia.html'
})
export class PostNoticiaComponent {

  /**
   * Noticia que está sendo exibida
   *
   */
  public selectedNotice = {};

  /**
   * Noticia que será exibida
   *
   */
  @Input() set notice( notice ) {
    // Seta a noticia selecionada
    this.selectedNotice = notice;

    // Verifica se possui uma imagem
    if ( !notice.image_link ) return;

    // Cria uma imagem
    const tempImage = new Image();
    tempImage.src = notice.image_link;

    // Quando a imagem original carregar, substitui a padrão
    tempImage.onload = ( img ) => {
      this.imageRef = tempImage.src;
    }
    tempImage.onerror = ( img ) => {
      this.selectedNotice['image_link'] = false;
    }
  };

  /**
   * Se deve mostrar o link para o gateway
   *
   */
  @Input() showGatewayLink = true;

  /**
   * Imagem da noticia
   *
   */
  public imageRef = 'assets/imgs/empty.jpg'

  /**
   * Método construtor
   *
   * @param navCtrl 
   * @param popoverCtrl 
   */
  constructor( public navCtrl: NavController,
               public popoverCtrl: PopoverController ) {
  }

  /**
   * Abre o perfil da noticia
   *
   */
  perfilNoticia( id ) {
    this.navCtrl.push( 'PerfilNoticiaPage', { id: id } );
  }

  /**
   * Abre o detalhe da noticia
   *
   * @param url 
   */
  openNotice() {
    this.navCtrl.push( 'NoticePage', { notice_id: this.selectedNotice['id'] } );
  }

  /**
   * Abre o popover
   *
   * @param event 
   */
  presentPopover( event ) {
    let popover = this.popoverCtrl.create( 'NoticiaPopoverPage', { notice: this.selectedNotice } );
    popover.present({
      ev: event
    });
  }
}

