import { SafariViewController } from '@ionic-native/safari-view-controller';
import { ToastController } from 'ionic-angular/components/toast/toast-controller';
import { AuthProvider } from './../../providers/auth/auth';
import { SocialSharing } from '@ionic-native/social-sharing';
import { GatewayProvider } from './../../providers/gateway/gateway';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { NoticeProvider } from './../../providers/notice/notice';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, LoadingController, Events, App, ViewController, AlertController } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-perfil-noticia',
  templateUrl: 'perfil-noticia.html',
})
export class PerfilNoticiaPage {

  // inicia as variaveis
  public idGateway;
  public gateway = {};
  public isFollowing = false;
  public totalPages = 1;
  public actualPage: any = 0;
  public isOnRequest = false;
  public notices = [];

  /**
   * construtor
   * 
   * Método construtor 
   */
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public modalCtrl: ModalController,
    public notice: NoticeProvider,
    public loadingCtrl: LoadingController,
    public viewCtrl: ViewController,
    public iab: InAppBrowser,
    public app: App,
    public gate: GatewayProvider,
    public socialSharing: SocialSharing,
    public auth: AuthProvider,
    public events: Events,
    public toastCtrl: ToastController,
    public alertCtrl: AlertController,
    public safariViewController: SafariViewController) {

    // pega o id do veiculo
    this.idGateway = this.navParams.get('id');
    if (!this.idGateway) this.back();
  }

  /**
   * __getGateway
   * 
   * Busca os dados vo veículo de noticia
   */
  __getGateway() {

    // cria o loading
    const loading = this.loadingCtrl.create({});
    loading.present();

    // busca os dados do veiculo
    this.gate.get(this.idGateway)
      .then(gateway => {
        this.gateway = gateway;
      })
      .catch(err => console.log(err))
      .then(() => loading.dismiss());
  }

  /**
   * Carrega novas noticias
   * 
   * @param event 
   * @param reset 
   */
  async loadNews(event: any = false, reset = false) {

    // Verifica se está e uma request
    if (this.isOnRequest) return;
    this.isOnRequest = true;

    // Verifica se deve resetar
    if (reset) {
      this.actualPage = 0;
      this.totalPages = 1;
    }

    try {

      // Obtem as noticias
      const notices = await this.notice.getGatewayNotices(this.idGateway, parseInt(this.actualPage) + 1)
      
      // verifica se deve resetar
      if (!reset) {
        for (let notice of notices.data) this.notices.push(notice);
      } else {
        this.notices = notices.data;
      }

      // seta as paginações
      this.totalPages = notices.total_pages;
      this.actualPage = parseInt(notices.page);

      // Indica que não está mais em um request
      this.isOnRequest = false;

      // Verifica se existe um evento
      if (event) event.complete();
    } catch (error) {
      console.log(error);
    }
  }

  /**
   * Carrega as noticias do veículo
   * 
   */
  ionViewDidEnter() {

    // carrega o veiculo de noticias
    this.__getGateway();

    // carrega as noticias
    this.loadNews(false, true);
  }

  /**
   * Mostra o toast
   * 
   */
  presentToast(msg) {
    let toast = this.toastCtrl.create({
      message: msg,
      duration: 3000,
      position: 'bottom'
    });
    toast.present();
  }

  /**
   * Faz o report
   * 
   */
  reportConfirmed() {

    // Mostra o loading
    const loading = this.loadingCtrl.create({ content: 'Denunciando notícia...' });
    loading.present();

    // Salva o report
    this.gate.report(this.idGateway)
      .then(success => {
        this.gateway['reported'] = true;
        this.presentToast('Denunciado');
      })
      .catch(err => {
        console.log(err);
        this.presentToast('Erro ao tentar denunciar');
      })
      .then(() => loading.dismiss());
  }

  /**
   * Mostra alerta de confirmação de report
   * 
   */
  alertConfirm() {
    let alert = this.alertCtrl.create({
      title: 'Denunciar',
      message: 'Deseja realmente denunciar essa noticia?',
      buttons: [{
        text: 'Sim',
        handler: () => {
          this.reportConfirmed();
        }
      }, {
        text: 'Não',
        role: 'cancel'
      }]
    });
    alert.present();
  }

  /**
   * Denuncia o veiculo de noticia
   * 
   */
  report() {

    // Verifica se o user esta logado
    if (!this.auth.user()) {
      this.navCtrl.push('LoginPage');
    } else {
      this.alertConfirm();
    }
  }

  /**
   * Abre pagina do veiculo na web
   * 
   */
  visitSite() {
    this.safariViewController.isAvailable()
      .then((available: boolean) => {
        if (available) {

          this.safariViewController.show({
            url: this.gateway['url'],
            hidden: false,
            animated: false,
            transition: 'curl',
            tintColor: '#ff000'
          })
            .subscribe((result: any) => {
              if (result.event === 'opened') console.log('Opened');
              else if (result.event === 'loaded') console.log('Loaded');
              else if (result.event === 'closed') console.log('Closed');
            }, (error: any) => console.log(error));
        }
        else {
          this.iab.create(this.gateway['url'], '_system');
        }
      })
  }

  /**
   * Compartilha a site com amigos
   * 
   * @param url 
   */
  shareGateway() {
    this.socialSharing.share(this.gateway['url'])
      .then(() => true)
      .catch(() => false);
  }

  /**
   * Segue um novo veiculo
   *
   */
  followVehicle() {

    // verifica está logado
    const user = this.auth.user();

    // verifica se veio user
    if (user) {
      const modal = this.modalCtrl.create('FollowModalPage', { gateway: this.gateway });
      modal.onDidDismiss(data => {

        // Verifica se salvou
        if (data) this.gateway['status'] = 'F';
      });
      modal.present();
    } else {
      this.navCtrl.push("LoginPage", { redirectPage: true });
    }
  }

  /**
   * Deixa de seguir o veiculo
   *
   */
  unfollowVehicle() {

    // Mostra o loading
    const loading = this.loadingCtrl.create({ content: 'Retirando inscrição...' });
    loading.present();

    // Faz a chamada
    this.gate.unfollow(this.idGateway)
      .then(success => {

        // Seta o status
        this.gateway['status'] = 'U';

        // Dispara o evento
        this.events.publish('atualizar');
      })
      .catch(err => console.log(false))
      .then(() => loading.dismiss());
  }

  /**
   * Chama os métodos de seguir
   * 
   */
  toggleFollow() {
    if (this.gateway['status'] == 'U') {
      this.followVehicle();
    } else {
      this.unfollowVehicle();
    }
  }

  /**
   * Muda o status
   * 
   */
  async toggleMuted() {

    // verifica está logado
    const user = this.auth.user();

    // verifica se veio user
    if (user) {

      // Exibe o loading
      const loading = this.loadingCtrl.create({
        content: this.gateway['muted'] ? 'Habilidatando função' : 'Desabilitando função'
      });
      loading.present();

      // Faz a chamada
      const method = this.gateway['muted'] ? 'unmute' : 'mute';
      try {
        await this.gate[method](this.gateway['id']);
        this.gateway['muted'] = !this.gateway['muted'];
      } catch (error) {
        console.log(error);
      }

      // Esconde o loading
      loading.dismiss();
    } else {
      this.navCtrl.push("LoginPage", { redirectPage: true });
    }
  }

  /**
   * back
   */
  back() {
    if (this.navCtrl.canGoBack()) {
      this.navCtrl.pop()
    } else this.navCtrl.setRoot('FeedPage');
  }
}

